.card-info{
	background: #F8FBFB;
	border-top: 1px solid #D0D7D8;
	border-bottom: 1px solid #D0D7D8;
	padding: 30px 0 50px;
	&__top{
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		align-items: flex-start;
		margin-bottom: 15px;
		&-links{
			display: flex;
			a{
				font-size: 16px;
				line-height: 1.6;
				color: #000000;
				padding: 11px 20px;
				border: 1px solid #CFD5D5;
				border-radius: 5px;
				margin-right: 20px;
				display: flex;
				align-items: center;
				background-color: #fff;
				transition: .3s;
				&::before{
					content: none;
				}
				.sprite-svg{
					margin-right: 10px;
					fill: #F74B4B;
				}
				&:hover{
					border-color: #989898;
				}
			}
		}
		&-price{
			display: flex;
			flex-wrap: wrap;
			justify-content: flex-end;
			.old{
				margin-right: 50px;
				margin-top: 8px;
				span{
					display: block;
					text-align: right;
					&:nth-child(1){
						font-weight: 500;
						font-size: 16px;
						line-height: 26px;
						color: #2E2E2E;
					}
					&:nth-child(2){
						font-weight: 300;
						font-size: 18px;
						line-height: 120%;
						text-decoration-line: line-through;
						color: #F74B4B;

					}
				}
			}
			.new{
				font-weight: 600;
				font-size: 42px;
				line-height: 1.4;
				color: #2E2E2E;
			}
			a{
				width: 100%;
				display: block;
				text-align: right;
				margin-top: 5px;
				line-height: 1;
				font-size: 16px;
				span{
					font-size: 16px;
					line-height: 26px;
					letter-spacing: 0.1em;
					color: rgba(51, 49, 49, 1);
					border-bottom: 1px dotted rgba(51, 49, 49, 1);
					transition: .3s;
					&:hover{
						border-color: transparent;
					}
				}
			}

		}
	}
	&__bottom{
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		align-items: center;
		&-block{
			max-width: 35%;
		}
		&-param{
			ul{
				display: flex;
				flex-wrap: wrap;
				li{
					margin-right: 9px;
					margin-bottom: 9px;
					span{
						line-height: 1.6;
						font-size: 16px;
						color: #000000;
						margin-right: 5px;
						&:nth-child(1){
							font-weight: 600;
						}
						&:nth-child(2){
						}
					}
				}
			}
		}
		&-btns{
			display: flex;
		}
	}

}
.compare--link{
	display: flex;
	align-items: center;
	font-weight: 500;
	font-size: 16px;
	line-height: 1.6;
	text-transform: uppercase;
    color: #333131;
	padding: 5px;
	.sprite-svg{
		margin-right: 16px;
		fill: #F74B4B;
	}
}
.link--rassrochka{
	display: flex;
	align-items: center;
	padding: 11px 23px;
	border: 2px solid #546062;
	border-radius: 5px;
	margin-right: 19px;
	font-weight: 500;
	font-size: 16px;
	line-height: 135%;
	color: #4D585A;
	.sprite-svg{
		margin-right: 16px;
		transition: .3s;
		width: 24px;
		height: 24px;
	}
	&:hover{
		color: #F74B4B;
		border-color: #F74B4B;
		.sprite-svg{
			fill: #F74B4B;
		}
	}
}
.link--buy{
	display: flex;
	align-items: center;
	color: #fff;
	background: #F74B4B;
	border: 2px solid #F74B4B;
	border-radius: 5px;
	padding: 11px 23px;
	.sprite-svg{
		margin-right: 16px;
		fill: #fff;
		transition: .3s;
		width: 24px;
		height: 24px;
	}
	&:hover{
		background-color: #fff;
		color: #F74B4B;
		.sprite-svg{
			fill: #F74B4B;
		}
	}
}
.card-info--for-mob{
	display: none;
}



.list-shop{
	position: relative;
	width: 100%;
	display: flex;
	justify-content: flex-end;
	&__name{
		border-bottom: 1px dotted #333131;
		transition: .3s;
		cursor: pointer;
		&:hover{
			border-bottom: 1px dotted transparent;
		}
		&.no-available{
			font-size: 18px;
		}

	}
	&__ul{
		position: absolute;
		right: 0%;
		top: calc(100% + 15px);
		width: 330px;
		box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.12);
		z-index: 100;
		display: none;
		&.open{
			display: block;
		}
		li{
			padding-left: 50px;
			background-position: 15px 25px;
			font-size: 14px;
		}
		&::before{
			content: '';
			width: 20px;
			height: 20px;
			position: absolute;
			top: -10px;
			left: calc(50% - 10px);
			background-color: #fff;
			z-index: 101;
			transform: rotate(45deg);
		}
		a{
			text-align: left;
			line-height: 120%;
			padding: 0;
			margin: 0;
			span{
				font-size: 14px;
				border-bottom: 0;
			}

		}
	}
}

@media screen and(max-width: 600px){
	.list-shop{
		justify-content: center;
		margin-bottom: 15px;
	}
	.list-shop__ul{
		width: 100%;
		a, span{
			font-size: 12px !important;
			text-align: left !important;
		}
	}
}